<template>
  <input type="text" 
    class="input-textfield-line" 
    :class='{ "input-textfield-invalid": v$.modelValue.$error }'
    :value="modelValue"
    @input="$emit('update:modelValue', $event.target.value.trim())"
    :placeholder='placeholder'
  >
  <div class='input-error-message' v-if='v$.modelValue.$error'>Поле має бути заповнене</div>
</template>

<script>
import useVuelidate from '@vuelidate/core'
import { required } from '@vuelidate/validators'
export default {
  props: {
    modelValue: { type: String, required: false, default: '' },
    placeholder: { type: String, required: false, default: '' },
  },
  data() { return {
    v$: useVuelidate(),
  }},
  validations() { return {
    modelValue: { required },
  }},
}
</script>