<template>
  <auth-layout>

    <div class="background"></div>

    <div class="auth-form">
      <form @submit.prevent='register()'>
        <div class="material-icons-outlined auth-icon">account_circle</div>
        <h2>РЕЄСТРАЦІЯ</h2>
        <input-text v-model='username' placeholder="Ваш логін" ref='username'/>
        <input-email v-model='email' ref='email'/>
        <input-password v-model='password' ref='password'/>
        <button type='submit' class='button-plain auth-button'>Створити акаунт</button>
        <small>Вже маєте акаунт? <router-link to='/auth'>Увійти!</router-link></small>
      </form>
    </div>

  </auth-layout>
</template>

<script>
import authLayout from '@/layouts/auth-layout.vue'
import inputText from '@/components/input-text.vue'
import inputEmail from '@/components/input-email.vue'
import inputPassword from '@/components/input-password.vue'
export default {
  components: { authLayout, inputText, inputEmail, inputPassword },
  data() { return {
    username: '',
    email: '',
    password: '',
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) }
  },
  methods: {
    async register() { 
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await this.$store.dispatch('reg', {
          username: this.username,
          email: this.email,
          password: this.password
        })
      }
    },
  },
}
</script>

<style lang="scss" scoped>
//* { border: 1px solid red }
.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(#0008, #0008), url('../assets/reg.png');
  background-position: left 20% top 60%;
  background-size: cover;
}

.auth-form {
  @media (min-width: 960px) {
    left: 25em;
  }
}
</style>